.Navbar{
    position: fixed;
    width: 100vw;
  }
  .Link{
    color:rgb(160, 160, 160);
    text-decoration: none;
    cursor: pointer;
    transition: 0.3s;
    padding: 2px 8px 5px 8px;
    border-radius: 6px;
  }
  .colorLink{
    color:rgb(255, 30, 0);
    text-decoration: none;
    cursor: pointer;
    padding: 2px 8px 5px 8px;
    transition: 0.3s;
    border-radius: 6px;
  }
  .Link:hover{
    color: white;
    background-color: rgb(19, 19, 19);
  }